bizagi = typeof bizagi !== "undefined" ? bizagi : {};
bizagi.webparts = typeof bizagi.webparts !== "undefined" ? bizagi.webparts : {};


bizagi.workportal.homeportalFramework.extend("bizagi.workportal.homeportalFramework", {}, {
  /**
                                                                                             *   Constructor
                                                                                             */
  init: function init(workportalFacade) {
    var self = this;
    self._super(workportalFacade);
    self.deviceType = "tablet";

    self.homePortalLayout = {};

    self.homePortalLayoutDashboard = {
      "menuHome": {
        "position": "homePortal",
        "direction": "left",
        "type": "view",
        "canvas": "menuHome",
        "title": "",
        "layout": "menu-default" },

      "dashBoard": {
        "position": "homePortal",
        "direction": "right",
        "type": "view",
        "canvas": "dashBoard",
        "title": "",
        "layout": "main-default" } };



    self.homePortalLayoutComplement = {
      "reportsChart": {
        "position": "homePortal",
        "direction": "right",
        "canvas": "reportsChart",
        "title": "Reports",
        "layout": "main-default-close" },

      "loadForm": {
        "position": "homePortal",
        "direction": "right",
        "type": "view",
        "canvas": "loadForm",
        "title": "Search Form",
        "layout": "main-default-close-action" },

      "renderTemplates": {
        "position": "homePortal",
        "direction": "right",
        "type": "view",
        "canvas": "renderTemplates",
        "title": "Render Template",
        "layout": "main-double-header" },

      "caseTemplates": {
        "position": "homePortal",
        "direction": "right",
        "type": "view",
        "canvas": "caseTemplates",
        "title": "",
        "layout": "main-double-header-cases" },

      "caseTemplatesOptions": {
        "position": "homePortal",
        "direction": "left",
        "type": "view",
        "canvas": "caseTemplatesOptions",
        "title": "",
        "layout": "side-default-back" },

      "stakeHolder": {
        "position": "homePortal",
        "canvas": "menuHome" },

      "shortCut": {
        "position": "menuHome",
        "canvas": "shortCut" },

      "searchesList": {
        "position": "menuHome",
        "canvas": "searchesList" },

      "stuff": {
        "position": ["dashBoard"],
        "canvas": ["stuff"] } };



    var req = self.viewRequirements(self._viewTaskFeedMode());
    self.homePortalWebpart = req.homePortalWebpart;
    self.menuWebpart = req.menuWebpart;
    self.homePortalLayoutTaskFeed = req.taskFeedLayout;

    self.homePortalLayoutSyncErrors = {
      "syncErrors": {
        "position": "homePortal",
        "direction": "right",
        "type": "view",
        "canvas": "syncErrors",
        "title": "Sync errors",
        "layout": "main-syncErrors-header" } };



    bizagi.util.mobility.leftPaneViewShown = $.proxy(self.leftPaneViewShown, self);
  },

  /**
      * constants for view mode in taskFeed
      */
  VIEW_TASKFEED_MODE: {
    CARDS: "cards",
    LIST: "list" },


  /**
                     * return the homePortal and layout for taskFeed depends of the View
                     * @param {String} viewToShow 
                     */
  viewRequirements: function viewRequirements(viewToShow) {
    var self = this,
    _homePortalLayoutTaskFeed = {
      "taskFeed": {
        "position": "homePortal",
        "direction": "left",
        "type": "view",
        "canvas": "taskFeed",
        "title": "",
        "layout": "side-default" },

      "summaryItem": {
        "position": "homePortal",
        "direction": "right",
        "canvas": "summaryItem",
        "type": "view",
        "title": "",
        "layout": "main-default" } },


    _homePortalLayoutCards = {
      "taskFeed": {
        "constructorName": "taskFeedCards",
        "position": "homePortal",
        "direction": "right",
        "type": "view",
        "canvas": "taskFeed",
        "title": "",
        "layout": "main-default" },

      "summaryItem": {
        "constructorName": "summaryItemCards",
        "position": "homePortal",
        "canvas": "summaryItem" } },


    _res = {},
    _default = {
      homePortalWebpart: {
        constructorName: undefined },

      menuWebpart: {
        constructorName: undefined },

      taskFeedLayout: _homePortalLayoutTaskFeed };

    switch (viewToShow) {
      case self.VIEW_TASKFEED_MODE.LIST:
        _res = _default;
        break;

      case self.VIEW_TASKFEED_MODE.CARDS:
        _res.homePortalWebpart = {
          constructorName: "homePortalCards" };

        _res.menuWebpart = {
          constructorName: "menuCards" };

        _res.taskFeedLayout = _homePortalLayoutCards;
        break;

      default:
        console.error("Error: view to show is not defined");
        _res = _default;
        break;}

    return _res;
  },

  /**
      * return the constructor name for homePortal
      */
  getHomePortalConstructorName: function getHomePortalConstructorName() {
    var self = this;
    return self.homePortalWebpart.constructorName;
  },

  /**
      * return the constructor name for menu
      */
  getMenuConstructorName: function getMenuConstructorName() {
    var self = this;
    return self.menuWebpart.constructorName;
  },

  /**
      * return the constructor name for summaryItem 
      */
  getSummaryItemConstructorName: function getSummaryItemConstructorName() {
    var self = this;
    return self.homePortalLayoutTaskFeed.summaryItem.constructorName;
  },

  _back: function _back(direction) {
    var self = this;

    // Add conditions to direction
    if (direction === "left") {
      $("#left-pane").data("kendoMobilePane").navigate("#:back");
    } else {
      $("#right-pane").data("kendoMobilePane").navigate("#:back");
    }
  },

  _backRenderTemplates: function _backRenderTemplates(transition) {
    var self = this;

    if (self.accumulatedContext.length > 1) {
      var context = self.accumulatedContext[self.accumulatedContext.length - 1];
      var accumulatedContext = self.popContext().context;
      if (context.navigateFromRender) {
        bizagi.kendoMobileApplication.navigate("renderSplitView");
        self.navigateFromRender = false;
      } else {
        var data = accumulatedContext[accumulatedContext.length - 1];
        var args = {
          data: data,
          calculateFilters: data.calculateFilters || false,
          filters: [],
          eventType: "DATA-NAVIGATION" };


        bizagi.webpart.publish("homeportalShow", {
          "what": "stuffTemplates",
          "title": args.data.displayName,
          "params": args,
          "preventNavigate": true });

      }
    } else {
      if (self.navigateFromRender) {
        bizagi.kendoMobileApplication.navigate("renderSplitView");
        self.navigateFromRender = false;
      }
    }

    self._back(transition);
  },

  _collapseHomePortalPane: function _collapseHomePortalPane() {
    $("#homePortal").data("kendoMobileSplitView").collapsePanes();
  },

  configureHandler: function configureHandler() {
    var self = this;
    self._super();
  },

  navigateToRenderTemplates: function navigateToRenderTemplates(pane) {
    $("#homePortal #right-pane").data("kendoMobilePane").navigate("renderTemplates");
    bizagi.kendoMobileApplication.navigate("homePortal");
  },

  /**
      * navigation control
      * @param navParams
      */
  homePortalNavigate: function homePortalNavigate(navParams) {
    var destination = navParams.url;
    var canvas = navParams.sender.element;
  },

  /**
      *
      * @param context
      */
  homePortalViewInit: function homePortalViewInit(context) {
    var self = this;
    self._super(context);
    self.destination = context.view.id.replace(/#/i, "");
    self.content = context.view.element;
    self.container = context.view.container;

    switch (self.destination) {
      case "summaryItem":
        $(self.content).on("click", ".bz-header-open-leftPane", function (event) {
          self._openLeftPane();
        });
        $(self.content).on("click", ".btn-back", function (event) {
          self._back();
        });
        $(self.content).on("touchend", "#bz-workarea-newcase", function (event) {
          event.preventDefault();
          self.showNewCase();
        });
        $(self.content).on("touchend", "#bz-workarea-changeview", function (event) {
          event.preventDefault();
          self.changeView();
        });
        $(self.content).on("touchend", "#bz-workarea-view-sync-panel", function (event) {
          event.preventDefault();
          self.showSyncPanel();
        });
        break;
      case "dashBoard":
        $(".km-view-title", self.content).children().text("DashBoard");
        $(self.content).on("click", ".bz-header-open-leftPane", function (event) {
          self._openLeftPane();
        });
        $(self.content).on("click", ".btn-back", function (event) {
          self._back();
        });
        $(self.content).on("click", "#bz-workarea-newcase", function (event) {
          self.showNewCase();
        });
        break;
      case "discussionCase":
        $(self.content).on("click", ".btn-back", function (event) {
          self._back();
        });
        $(self.content).on("click", "#bz-workarea-newcase", function (event) {
          self.showNewCase();
        });
        break;
      case "menuHome":
        $(self.content).data("kendoMobileView").header.on("click", ".btn-menu", function (event) {
          self.showMenu();
        });
        break;
      case "taskFeed":
        $(self.content).on("touchend", ".bz-header-left-btn.btn-open-drawer", function (event) {
          event.preventDefault();
          self.showMenu();
        });

        $(self.content).on("touchend", "#bz-workarea-newcase", function (event) {
          event.preventDefault();
          self.showNewCase();
        });
        $(self.content).on("touchend", "#bz-workarea-changeview", function (event) {
          event.preventDefault();
          self.changeView();
        });
        $(self.content).on("touchend", "#bz-workarea-view-sync-panel", function (event) {
          event.preventDefault();
          self.showSyncPanel();
        });
        break;
      case "syncErrors":
        $(self.content).on("click", ".btn-back", function (event) {
          // Only in main view (activities + summary) not in cards
          if (self._viewTaskFeedMode() === self.VIEW_TASKFEED_MODE.LIST) {
            // for show taskFeed panel
            bizagi.webpart.publish("bz-left-panel-show");
            self.destination = 'summaryItem';
          } else
          {
            self.destination = 'taskFeed';
          }
          bizagi.webpart.publish("bz-update-list-cases", { updateDataList: true });
          self._back();
        });
        break;
      case "caseTemplatesOptions":
        $(self.content).on("click", ".bz-header-left-btn.btn-back", function (event) {
          self._back("right");
          self._back("left");
        });
        break;
      case "caseTemplates":
        $(self.content).on("click", ".bz-header-open-leftPane", function (event) {
          self._openLeftPane();
        });
        $(self.content).on("click", "#bz-workarea-newcase", function (event) {
          self.showNewCase();
        });
        break;
      case "renderTemplates":
        $(self.content).on("click", ".btn-back", function (event) {
          self._backRenderTemplates();
        });
        $(self.content).on("click", "#bz-workarea-newdata", function (event) {
          bizagi.webpart.publish("newData");
        });
        $(self.content).on("click", ".bz-header-open-leftPane", function (event) {
          self._openLeftPane();
        });
        break;
      case "reportsChart":
        $(self.content).on("click", ".btn-close", function (event) {
          $("iframe", ".bz-wp-reports-chart").remove();
          $(".bz-wp-reports-chart", self.content).empty();

          self.reportsChartEndLeftPanel();
        });
        break;}

  },

  /**
      * when view is shown
      * @param navParams
      */
  homePortalViewShow: function homePortalViewShow(navParams) {
    var self = this;
    var navLength;
    self._super(navParams);
    self.destination = navParams.view.id.replace(/#/i, "");
    self.content = navParams.view.element;
    self.container = navParams.view.container;


    switch (self.destination) {
      case "renderTemplates":
        $(".km-view-title", self.content).children().text("DashBoard");
        break;

      case "caseTemplates":
        //fix navigation bug
        navLength = navParams.sender.history.length;
        if (navParams.sender.history[navLength - 1] === navParams.sender.history[navLength - 3]) {
          navParams.sender.history.pop();
          navParams.sender.history.pop();
        }

        $("#left-pane").getKendoMobilePane().navigate("caseTemplatesOptions");
        break;

      case "reportsChart":
        navLength = navParams.sender.history.length;
        if (navParams.sender.history[navLength - 1] === navParams.sender.history[navLength - 3]) {
          navParams.sender.history.pop();
        }

        $(".km-view-title", self.content).children().text("");

        $(".btn-close", self.content).unbind("click").bind("click", function () {
          $("iframe", ".bz-wp-reports-chart").remove();
          $(".bz-wp-reports-chart", self.content).empty();

          $(".km-view-title", self.content).children().text("");
          self._back();
        });

        self.reportsChartEndLeftPanel = self.reportsChartStartLeftPanel();
        break;

      case "syncErrors":
        break;

      default:
        break;}


    self.getMenuWebpartInstance();
  },

  /**
      * hide the left panel and return the method to show again if is actually shown, else omit everything
      */
  reportsChartStartLeftPanel: function reportsChartStartLeftPanel() {
    var self = this,
    _leftPanel = $("#left-pane", self.canvas),
    _isVisible = _leftPanel.css("display") !== "none",
    _action = _isVisible ? _leftPanel.toggle.bind(_leftPanel) : function () {};
    _action();
    return _action;
  },

  leftPaneViewShown: function leftPaneViewShown(navParams) {
    var self = this;

    self.leftDestination = navParams.view.id.replace(/#/i, "");
    self.leftContent = navParams.view.element;
  },

  _openLeftPane: function _openLeftPane() {
    var self = this;

    var view = bizagi.kendoMobileApplication.view();
    view.expandPanes();
  },

  homePortalLayoutInit: function homePortalLayoutInit(context) {
  },

  /**
      * Close all open drawers
      */
  closeDrawers: function closeDrawers() {
    $("#newCase-drawer").data("kendoMobileDrawer").hide();
  },

  /**
      * For new case
      */
  showNewCase: function showNewCase() {
    this.showDrawer("newCase");
  },

  /**
      * For showSyncPanel
      */
  showSyncPanel: function showSyncPanel() {
    this.showDrawer("syncErrors");
  },

  /**
      * For showMenu
      */
  showMenuPanel: function showMenuPanel() {
    this.showDrawer("menu");
  },

  /**
      * toggle the list view to cards
      */
  changeView: function changeView() {
    var self = this,
    _view = self.VIEW_TASKFEED_MODE,
    _newView =
    self._viewTaskFeedMode() === _view.CARDS ? _view.LIST : _view.CARDS;
    self._viewTaskFeedMode(_newView);
    self.isChangingView(true);
    location.reload();
  },

  /**
      * return a localStorage value of the key and if the val is set the value is overrided
      * if the localStorage does not have value the defaultVal is used
      */
  _getSetLocalStorage: function _getSetLocalStorage(key, val, defaultVal) {
    var _actualValue = function _actualValue() {return localStorage.getItem(key);},
    _setValue = function _setValue(val) {return localStorage.setItem(key, val);},
    _getValue = function _getValue() {
      if (_actualValue() === null)
      _setValue(defaultVal);
      return _actualValue();
    };
    if (val !== undefined)
    _setValue(val);
    return _getValue();
  },

  /**
      * return the view for taskFeed and let set the new value too
      */
  _viewTaskFeedMode: function _viewTaskFeedMode(val) {
    var self = this,
    _key = "homePortalView",
    _default = self.VIEW_TASKFEED_MODE.LIST;

    return bizagi.util.isMobileInboxCardViewSupported() ?
    self._getSetLocalStorage(_key, val, _default) :
    _default;
  },

  /**
      * return the view for taskFeed and let set the new value too
      */
  isChangingView: function isChangingView(val) {
    var self = this,
    _key = "homePortalChangingView",
    _default = false;
    return self._getSetLocalStorage(_key, val, _default) == "true";
  },

  /**
      * Show menu sidebar
      */
  showMenu: function showMenu() {
    var self = this;
    self.getMenuWebpartInstance().done(function (instance) {
      instance.showModal();
    });
  },

  /**
      * if there is not an instance, create a new and resolve else just resolve
      * this includes Native menu, so please NO override the constructorName here
      */
  getMenuWebpartInstance: function getMenuWebpartInstance() {
    var self = this,
    _isReady = self.menuWebpartInstance !== undefined,
    _newInstance = self.workportalFacade.executeWebpart.bind(self.workportalFacade, {
      webpart: "menu",
      canvas: $("body"),
      isCardsLayout: self.homePortalWebpart.constructorName === "homePortalCards" }),

    _saveInstance = function _saveInstance(instance) {self.menuWebpartInstance = instance;},
    defer = _isReady ? new $.Deferred() : _newInstance();

    _isReady ? defer.resolve(self.menuWebpartInstance) : defer.then(_saveInstance);

    return defer;
  } });